/* Custom reset.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a:focus,
a:active a:active:focus,
button:active,
button:active:focus,
button:focus,
input:active,
input:active:focus,
input:focus,
textarea:active,
textarea:active:focus,
textarea:focus,
input,
button,
a,
textarea {
  outline: none !important;
  outline-color: transparent !important;
  outline-width: 0 !important;
  outline-style: none !important;
}

a {
  -webkit-touch-callout: none;
  user-select: none;
}

.document {
  font-size: 62.5%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

ul,
ol {
  list-style-type: none;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  border: none;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* Scrollbar styling */
* {
  scrollbar-width: thin;
  scrollbar-color: #6583ED transparent;
}
*::-webkit-scrollbar {
  display: block;
  overflow: auto;
  width: 0.4rem;
  height: 0;
}
*::-webkit-scrollbar-track, *::-webkit-scrollbar-track-piece {
  background-color: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: #6583ED;
  border-radius: 1rem;
}
*::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
  background-color: #214ce5;
}
*::-webkit-scrollbar-button, *::-webkit-scrollbar-corner, *::-webkit-resizer {
  display: none;
}

::selection {
  background: #6583ED;
}

::-moz-selection {
  background: #6583ED;
}

.document-body {
  min-width: 100vw;
  overflow-x: hidden;
  background: #FFFFFF;
  font-family: "Raleway", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.5;
  color: #343434;
  font-feature-settings: "lnum";
}

/* Frequent classes */
.container {
  margin: 0 auto;
  width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #7E7E7E;
  border-radius: 30px;
  width: 216px;
  height: 57px;
  text-align: center;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 600;
  color: #7E7E7E;
}
.button:hover {
  color: #6583ED;
  border-color: #343434;
}
.button:active {
  background-color: #CCD6F8;
}

.header-container {
  padding: 24px;
  display: flex;
  justify-content: space-between;
}
.header .header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 116px;
}
.header .address_email {
  display: none;
}

.logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.logo__wrapper {
  width: 253px;
  height: 62px;
  overflow: hidden;
}

.address {
  font-style: normal;
  font-size: 20px;
  font-weight: medium;
  text-align: right;
}
/* Adaptive */
@media screen and (min-width: 576px) {
  .container {
    width: 540px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 720px;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 960px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1140px;
  }
}
@media screen and (min-width: 1400px) {
  .container {
    width: 1320px;
  }
}